import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import { Container, Grid, Box, Typography } from "@material-ui/core";

const testimonials_data = [
  {
    name: "Lorem ipsum",
    testimonialsrc: "https://picsum.photos/id/218/800/600",
    description: `Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor explicabo fuga cum eos provident vitae, 
    necessitatibus repellat sapiente nulla deserunt sequi optio distinctio earum.`,
  },
  {
    name: "Lorem ipsum",
    testimonialsrc: "https://picsum.photos/id/201/800/600",
    description: `Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor explicabo fuga cum eos provident vitae, 
    necessitatibus repellat sapiente nulla deserunt sequi optio distinctio earum.`,
  },
  {
    name: "Lorem ipsum",
    testimonialsrc: "https://picsum.photos/id/20/800/600",
    description: `Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor explicabo fuga cum eos provident vitae, 
    necessitatibus repellat sapiente nulla deserunt sequi optio distinctio earum.`,
  },
  {
    name: "Lorem ipsum",
    testimonialsrc: "https://picsum.photos/id/202/800/600",
    description: `Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor explicabo fuga cum eos provident vitae, 
    necessitatibus repellat sapiente nulla deserunt sequi optio distinctio earum.`,
  },
];

const ClientTestimonials = () => {
  var settings = {
    dots: true,
    navs: true,
    swipeToSlide: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <Container
      className="testimonials_outer home_product_slide_comm pt_100 pb_100"
      maxWidth="lg"
    >
      <Grid container spacing={4} justify="center">
        <Grid item xs={10}>
          <Box pb={3}>
            <Typography variant="h2" align="center" className="main_title">
              Testimonials
            </Typography>
          </Box>
        </Grid>
        <Box className="testimonials_inn" style={{ width: "100%" }}>
          <Slider {...settings} className="slider_outer transform_y">
            {testimonials_data.map((testimonials_data, ind) => (
              <div className="slide_item" key={ind}>
                <Box className="product_container">
                  <Box className="product_inn">
                    <Box className="product_pic_outer">
                      <img
                        src={testimonials_data.testimonialsrc}
                        alt="product image"
                        className=""
                      />
                    </Box>

                    <Box>
                      <Typography variant="body1" align="center" gutterBottom>
                        {testimonials_data.description}
                      </Typography>

                      <Typography variant="h5" align="center">
                        {testimonials_data.name}
                      </Typography>
                    </Box>
                  </Box>
                </Box>
              </div>
            ))}
          </Slider>
        </Box>
      </Grid>
    </Container>
  );
};

export default ClientTestimonials;
