import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import { Container, Grid, Box, Typography } from "@material-ui/core";

const new_launches_data = [
  {
    title: "Product 01",
    description: `Lorem, ipsum dolor sit amet consectetur adipisicing
      elit. Dignissimos, repudiandae velit architecto ipsam
      nemo repellat beatae qui consequuntur voluptate nobis
      quia? Corrupti obcaecati nemo eos repudiandae officia
      soluta assumenda harum.`,
    productsrc: "https://picsum.photos/id/20/800/600",
  },
  {
    title: "Product 02",
    description: `Lorem, ipsum dolor sit amet consectetur adipisicing
    elit. Dignissimos, repudiandae velit architecto ipsam
    nemo repellat beatae qui consequuntur voluptate nobis
    quia? Corrupti obcaecati nemo eos repudiandae officia
    soluta assumenda harum.`,
    productsrc: "https://picsum.photos/id/20/800/600",
  },
  {
    title: "Product 03",
    description: `Lorem, ipsum dolor sit amet consectetur adipisicing
    elit. Dignissimos, repudiandae velit architecto ipsam
    nemo repellat beatae qui consequuntur voluptate nobis
    quia? Corrupti obcaecati nemo eos repudiandae officia
    soluta assumenda harum.`,
    productsrc: "https://picsum.photos/id/20/800/600",
  },
  {
    title: "Product 04",
    description: `Lorem, ipsum dolor sit amet consectetur adipisicing
    elit. Dignissimos, repudiandae velit architecto ipsam
    nemo repellat beatae qui consequuntur voluptate nobis
    quia? Corrupti obcaecati nemo eos repudiandae officia
    soluta assumenda harum.`,
    productsrc: "https://picsum.photos/id/20/800/600",
  },
];

const ProductLayout2 = () => {
  var settings = {
    dots: false,
    navs: true,
    swipeToSlide: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <Container className="new_launches_outer  pt_100" maxWidth="lg">
      <Grid container spacing={4} justify="center">
        <Grid item xs={10}>
          <Box pb={3}>
            <Typography variant="h2" align="center" className="main_title">
              Product Layout two
            </Typography>
          </Box>
        </Grid>
        <Box className="new_launches_inn" style={{ width: "100%" }}>
          <Slider {...settings} className="slider_outer transform_y">
            {new_launches_data.map((new_launches_data, ind) => (
              <div className="slide_item" key={ind}>
                <Box className="product_container">
                  <Grid container>
                    <Grid item xs={12} md={4}>
                      <Box className="product_pic_outer">
                        <img
                          src={new_launches_data.productsrc}
                          alt="product image"
                          className="img-fluid"
                        />
                      </Box>
                    </Grid>
                    <Grid item xs={12} md={8} className="new_launches_desc">
                      <Box className="new_launches_desc_inn">
                        <Typography variant="h3">
                          {new_launches_data.title}
                        </Typography>
                        <Typography variant="body1">
                          {new_launches_data.description}
                        </Typography>
                      </Box>
                    </Grid>
                  </Grid>
                </Box>
              </div>
            ))}
          </Slider>
        </Box>
      </Grid>
    </Container>
  );
};

export default ProductLayout2;
